:root {
  --main-theme-color: #d4129a;
}

* {
  font-family: Trebuchet MS;
  color: #d6d8da;
}

body {
  background: #24292e;
}

.card-container {
  display: flex;
  flex-wrap: wrap;
  position: absolute;
  z-index: -1;
  margin-bottom: 102px;
}

.form-container {
  background-color: black;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding-left: 15px;
  padding-right: 15px;
  padding-bottom: 15px;
}

.form-inline {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}

.form-inline label {
  margin: 5px 10px 5px 0;
}

.form-inline input {
  vertical-align: middle;
  margin: 0 10px 0 0;
  padding: 5px;
  background-color: #31363f;
  border: none;
  border-radius: 5px;
}

.form-inline button {
  padding: 5px 20px;
  background-color: var(--main-theme-color);
  border: none;
  border-radius: 5px;
}

.form-inline button:hover {
  background-color: #d15dae;
  transition: 0.3s all;
}

@media (max-width: 800px) {
  .form-inline input {
    margin: 10px 0 0 0;
  }
  .form-inline button {
    margin: 10px 0 0 0;
  }
  .form-inline {
    flex-direction: column;
    align-items: stretch;
  }
  .form-inline label {
    display: none;
  }
  .card-container {
    margin-bottom: 182px;
  }
}

.card {
  height: 350px;
  width: 250px;
  border-radius: 0.5rem;
  margin: 10px;
  padding: 0.5rem;
  font-size: 14px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  background: #31363f;
}

